@keyframes rotate-text {
    0% {
      transform: rotateZ(180deg);
    }
    100% {
      transform: rotateZ(0);
      opacity: 1;
      color: rgb(217,75,107);
      text-shadow: 1px 1px 2px rgba(0,0,0, 1);
    }
}

@keyframes navitemin {
    from {
    }

    to {
        opacity: 1;
        transform: scale(1) translate(0,0) rotate(0deg);
    }
}

@keyframes upperform {
    from {
        transform: scale(0.6);
    }

    to {
        transform: scale(1);
    }
}

@keyframes small {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(0.9);
    }
}

@keyframes grow {
    from {
        transform: scale(0.9);
    }

    to {
        transform: scale(1);
    }
}